@font-face {
  font-family: "Manrope";
  src: url("Manrope-Bold.eot");
  src: local("Manrope Bold"), local("Manrope-Bold"),
    url("Manrope-Bold.woff2") format("woff2"),
    url("Manrope-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

