/* @import-normalize; */
/* bring in normalize.css styles */

:root {
  --black-color: #000000;
  --white-color: #f7f7fb;
  --grey-color: #8a8a89;
  --light-blue-color: #3470ff;
  --dark-blue-color: #0b44cd;
  --violet-color: #450bcd;
  --hero-text-color: #ffffff;
  --header-text-color: #ff6b00;
  --test: #fa3131;
  --transition: 500ms ease-in;
}

body {
  /* overflow: hidden; */
  font-family: "Manrope", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  -webkit-tap-highlight-color: transparent;
  -moz-tap-highlight-color: transparent;
  /* color: var(--white-color); */
  width: 100%;
  height: 100vh;
  padding: 0;
  margin: 0;
}

.move {
  transform: translate(0px, -40px);
}
